"use client";

import React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";

import {
  Bars3Icon,
  ChevronDoubleLeftIcon,
  ChevronDoubleRightIcon,
  MagnifyingGlassIcon,
  ClockIcon,
  DocumentMagnifyingGlassIcon,
  SunIcon,
  MoonIcon,
} from "@heroicons/react/24/outline";

type Theme = "light" | "dark";

type TopbarProps = {
  collapsed: boolean;
  onToggleSidebar: () => void;
  onOpenMobile: () => void;

  theme: Theme;
  onToggleTheme: () => void;
};

function getTitleFromPath(pathname: string) {
  if (pathname === "/") return "Compare Documents";
  if (pathname === "/history") return "History";
  if (pathname.startsWith("/compare/")) return "Compare Detail";
  if (pathname === "/dashboard") return "Dashboard";
  if (pathname === "/reports") return "Reports";
  if (pathname === "/documents") return "Documents";
  return "Dashboard";
}

function getBreadcrumb(pathname: string) {
  const crumbs: { label: string; href?: string }[] = [{ label: "Home", href: "/" }];

  if (pathname === "/history") {
    crumbs.push({ label: "History" });
  } else if (pathname.startsWith("/compare/")) {
    const id = pathname.split("/").filter(Boolean)[1];
    crumbs.push({ label: "History", href: "/history" });
    crumbs.push({ label: `Detail #${id || ""}` });
  } else if (pathname === "/") {
    crumbs.push({ label: "Compare" });
  } else if (pathname === "/dashboard") {
    crumbs.push({ label: "Dashboard" });
  } else if (pathname === "/reports") {
    crumbs.push({ label: "Reports" });
  } else if (pathname === "/documents") {
    crumbs.push({ label: "Documents" });
  }

  return crumbs;
}

export default function Topbar({
  collapsed,
  onToggleSidebar,
  onOpenMobile,
  theme,
  onToggleTheme,
}: TopbarProps) {
  const pathname = usePathname();
  const title = getTitleFromPath(pathname);
  const crumbs = getBreadcrumb(pathname);

  return (
    <header className="sticky top-0 z-40 border-b border-slate-200 bg-white dark:border-slate-800 dark:bg-slate-950">
      <div className="h-16 px-4 md:px-6 flex items-center justify-between gap-3">
        {/* Left */}
        <div className="flex items-center gap-2">
          <button
            type="button"
            onClick={onOpenMobile}
            className="md:hidden inline-flex h-10 w-10 items-center justify-center rounded-xl border border-slate-200 bg-white hover:bg-slate-50
                       dark:border-slate-800 dark:bg-slate-900 dark:hover:bg-slate-800"
            aria-label="Open sidebar"
          >
            <Bars3Icon className="h-6 w-6 text-slate-900 dark:text-slate-100" />
          </button>

          <button
            type="button"
            onClick={onToggleSidebar}
            className="hidden md:inline-flex h-10 items-center gap-2 rounded-xl px-3 border border-slate-200 bg-white hover:bg-slate-50
                       dark:border-slate-800 dark:bg-slate-900 dark:hover:bg-slate-800"
            aria-label="Toggle sidebar"
          >
            {collapsed ? (
              <>
                <ChevronDoubleRightIcon className="h-5 w-5" />
                <span className="text-sm font-extrabold">Expand</span>
              </>
            ) : (
              <>
                <ChevronDoubleLeftIcon className="h-5 w-5" />
                <span className="text-sm font-extrabold">Collapse</span>
              </>
            )}
          </button>

          <div className="ml-1">
            <div className="text-sm md:text-base font-extrabold leading-tight">
              {title}
            </div>

            <nav className="hidden sm:flex items-center gap-1 text-xs font-semibold text-slate-500 dark:text-slate-400">
              {crumbs.map((c, idx) => (
                <React.Fragment key={`${c.label}-${idx}`}>
                  {idx > 0 && <span className="text-slate-300 dark:text-slate-700">/</span>}
                  {c.href ? (
                    <Link href={c.href} className="hover:text-slate-900 dark:hover:text-slate-100">
                      {c.label}
                    </Link>
                  ) : (
                    <span className="text-slate-700 dark:text-slate-300">{c.label}</span>
                  )}
                </React.Fragment>
              ))}
            </nav>
          </div>
        </div>

        {/* Center */}
        <div className="hidden lg:flex flex-1 max-w-xl">
          <div className="relative w-full">
            <MagnifyingGlassIcon className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-slate-400" />
            <input
              placeholder="Search (UI only)..."
              className="w-full rounded-xl bg-white px-10 py-2 text-sm font-semibold text-slate-900 border border-slate-200
                         focus:outline-none focus:ring-2 focus:ring-slate-900
                         dark:bg-slate-900 dark:text-slate-100 dark:border-slate-800 dark:focus:ring-slate-200"
            />
          </div>
        </div>

        {/* Right */}
        <div className="flex items-center gap-2">
          <Link
            href="/"
            className="hidden sm:inline-flex items-center gap-2 rounded-xl bg-slate-900 px-3 py-2 text-sm font-extrabold text-white hover:opacity-95
                       dark:bg-slate-100 dark:text-slate-900"
          >
            <DocumentMagnifyingGlassIcon className="h-5 w-5" />
            Compare
          </Link>

          <Link
            href="/history"
            className="hidden sm:inline-flex items-center gap-2 rounded-xl border border-slate-200 bg-white px-3 py-2 text-sm font-extrabold text-slate-900 hover:bg-slate-50
                       dark:border-slate-800 dark:bg-slate-900 dark:text-slate-100 dark:hover:bg-slate-800"
          >
            <ClockIcon className="h-5 w-5" />
            History
          </Link>

          <button
            type="button"
            onClick={onToggleTheme}
            className="inline-flex h-10 w-10 items-center justify-center rounded-xl border border-slate-200 bg-white hover:bg-slate-50
                       dark:border-slate-800 dark:bg-slate-900 dark:hover:bg-slate-800"
            aria-label="Toggle theme"
            title="Toggle theme"
          >
            {theme === "dark" ? (
              <SunIcon className="h-5 w-5 text-slate-100" />
            ) : (
              <MoonIcon className="h-5 w-5 text-slate-900" />
            )}
          </button>
        </div>
      </div>
    </header>
  );
}
